<script>
	import katex from "katex";
    import { onMount } from 'svelte';
    export let m;
	export let displayMode = false;
    let k;
	
	const options = {
		displayMode: displayMode,
		throwOnError: false
    };

    onMount(() => {
        k = katex.renderToString(m, options);
        // console.log(`got k = ${k}`);
    });
	
</script>

